.choice{display:flex;align-items:stretch;justify-content:center;margin-top:15px}.choice_large{max-width:40%}.choice__card{display:flex;max-width:35%;flex:1 1 28%;flex-direction:column;justify-content:space-between;margin-right:15px;background:#fff;border-radius:15px;box-shadow:-4px 4px 4px rgba(0,0,0,.6117647059);cursor:pointer;transition:transform .3s ease,box-shadow .3s ease}.choice__card:hover{box-shadow:0 0 20px 9px rgba(215,215,215,.8196078431);transform:scale(1.05)}.choice__card:focus{box-shadow:0 0 20px 9px rgba(215,215,215,.8196078431);outline:none;transform:scale(1.1)}.choice__card:last-child{margin-right:unset}.choice__list{padding:15px 15px 15px 10px}.choice__item{font-size:16px;line-height:130%;text-transform:uppercase}.choice__item::before{margin-right:5px;content:"✓"}.choice__item_text{padding:15px}.choice__item_text::before{content:""}.choice__title{padding:10px;border-bottom-left-radius:15px;border-bottom-right-radius:15px;color:#fff;font-size:24px;font-weight:bold;text-align:center;text-transform:uppercase}.choice__title_blue{background:linear-gradient(45deg, rgb(47, 29, 253) 0%, rgb(16, 98, 246) 46%, rgb(69, 153, 252) 100%)}.choice__title_red{background:linear-gradient(45deg, rgb(131, 58, 180) 0%, rgb(253, 29, 29) 0%, rgb(253, 50, 35) 46%, rgb(252, 176, 69) 100%)}.choice__title_green{background:linear-gradient(45deg, rgb(25, 185, 7) 0%, rgb(25, 185, 7) 46%, rgb(25, 185, 7) 100%)}.choice__title_orange{background:linear-gradient(45deg, rgb(250, 177, 59) 0%, rgb(238, 125, 12) 63%, rgb(255, 252, 0) 100%)}