// Size
$size: 15px;
$size2: $size * 2 ;
$size3: $size * 3 ;
$size4: $size * 4 ;
$icon-size: 25px;
$radius-size: 15px;
$signal-size: 20px;

// Color
$light-green: #54d160;
$tundora: #434343;
$silver: #c4c4c4;
$black: #000;
$picton-blue: #2cc3e4;
$smalt-blue: #4a6980;
$mine-shaft: #2b2b2b;
$red: #ff0202;
$mercury: #e9e9e9;
$orenge: #e38d3d;
$dark-yellow: #e1cf27;
$light-silver: #f5f5f5;
$dark-green: #238000;
$blue: blue;
$white: #fff;

// Gradient
// 1
$col-1: rgb(131 58 180 / 100%);
$col-2: rgb(253 29 29 / 100%);
$col-3: rgb(253 50 35 / 100%);
$col-4: rgb(252 176 69 / 100%);

// 2
$grad-blue-1: rgb(47 29 253 / 100%);
$grad-blue-2: rgb(16 98 246 / 100%);
$grad-blue-3: rgb(69 153 252 / 100%);

// 3
$grad-green-1: rgb(25 185 7 / 100%);
$grad-green-2: rgb(19 199 43 / 100%);
$grad-green-3: rgb(33 184 139 / 100%);

// 4
$grad-orange-1: rgb(250 177 59 / 100%);
$grad-orange-2: rgb(238 125 12 / 100%);
$grad-orange-3: rgb(255 252 0 / 100%);

// Components
$bg-header: #959595;
$bg-white: $white;
$bg-list: $silver;
$bg-app: $mine-shaft;
$bg-nav: $tundora;
$bg-content: $mercury;
$bg-line: $light-silver;
$nav-active-link: $smalt-blue;
$active: $light-green;
$signal-normal: $dark-green;
$empty: $silver;
$warning: $red;
$prime: $smalt-blue;

// Choice card
$choice-card-bg-red: linear-gradient(45deg, $col-1 0%, $col-2 0%, $col-3 46%, $col-4 100%);
$choice-card-bg-blue: linear-gradient(45deg, $grad-blue-1 0%, $grad-blue-2 46%, $grad-blue-3 100%);
$choice-card-bg-green: linear-gradient(45deg, $grad-green-1 0%, $grad-green-1 46%, $grad-green-1 100%)  ;
$choice-card-bg-orange: linear-gradient(45deg, $grad-orange-1  0%, $grad-orange-2 63%, $grad-orange-3  100%);
$choice-card-shadow: 0 0 20px 9px #d7d7d7d1;

// Shadow

$shadow-prime: -4px 4px 4px #0000009c;
$shadow-white: 4px 4px 4px $light-silver;
$shadow-input: 0 0 4px 3px #5680929c;
$shadow-input: -5px 20px 8px #0000009c;

// Text
$text-color-grey: #636363;
$text-color-black: #000;
$text-color-white: #fff;
$nav-line-height: 15px;
$title-size: 25px;

// Fonts
$font-roboto: roboto, sans-serif;
$font-work: "Work Sans", sans-serif;

// panel and corrections and schemes
$color-signal-yellow: #fefe00;
$color-signal-green: #238000;
$color-signal-red: #ff0202;
$color-signal-black: #000;
$color-signal-prime: $smalt-blue;
$color-signal-gray: #9d9d9d;
$color-signal-blue: $blue;

// @media

$screen-laptop: 1038px;
$screen-tablet: 768px;
$screen-mobile: 425px;

// Z-INDEX

$z-index-normal: 1;
$z-index-legend: 10;
$z-index-toggle-legend: 20;
$z-index-auth: 50;
$z-index-keyboard: 100;
$z-index-toggle-keyboard: 110;
$z-index-spinner: 500;
$border-grey: #e8e8e8;
$bg-input-grey: #d0d0d0;
$bg-input-light: #fff;
$bg-active-line: rgb(40 47 56 / 21%);
$bg-nav-color: #2c2c2c;
$bg-grid-title: #f2f2f2;
$bg-app: #ebebeb;
$bg: #fff;
$wrong: red;
$box-shadow: 4px 4px 4px rgb(0 0 0 / 25%);
$box-shadow-click: 1px 1px 2px rgb(0 0 0 / 25%);
$box-shadow-hover: 5px 5px 5px 1px rgb(0 0 0 / 20%);
$border: 0.5px solid #e8e8e8;
